<template>
	<div class="order">
		<el-row :gutter="10" >
			<el-col :span="6" v-for="itemc in rows" >
				<div class="bg-purple grid-content"  @click="orderHander(itemc.table)">
					<el-tag style="font-size: 24px;" type="danger">{{itemc.table}}</el-tag>	
					<el-carousel height="220px" style="margin-top: 10px;" direction="vertical">
						<el-carousel-item v-for="item in itemc.list">
							<el-tag style="font-size: 20px;" class="red :item.statue==2"  type="getType(item)">{{ item.footName }}X{{item.num}}</el-tag>
						</el-carousel-item>
					</el-carousel>
				</div>
			</el-col>   
		 </el-row>
	</div>
</template>

<script>
	import {listOrderMain,
	listOrderSkuByOrderId,
	finishOrder,
	updateOrder,
	saveFoodOrderVO}   from "../../api/index.js"
	import {orderVO} from '../../entity/orderVO.js'
	export default {
		data(){
			return {
				orderVO:{},
				rows:[
						{table:'1',list:[],orderVO:{}},
						{table:'2',list:[],orderVO:{}},
						{table:'3',list:[],orderVO:{}},
						{table:'4',list:[],orderVO:{}},
						{table:'5',list:[],orderVO:{}},
						{table:'6',list:[],orderVO:{}},
						{table:'7',list:[],orderVO:{}},
						{table:'8',list:[],orderVO:{}},
						{table:'9',list:[],orderVO:{}},
						{table:'10',list:[],orderVO:{}},
						{table:'11',list:[],orderVO:{}},
						{table:'12',list:[],orderVO:{}},
						{table:'13',list:[],orderVO:{}},
						{table:'14',list:[],orderVO:{}},
						{table:'15',list:[],orderVO:{}},
						{table:'16',list:[],orderVO:{}},
						{table:'17',list:[],orderVO:{}},
						{table:'18',list:[],orderVO:{}},
						{table:'19',list:[],orderVO:{}},
						{table:'20',list:[],orderVO:{}},
						{table:'21',list:[],orderVO:{}},
						{table:'22',list:[],orderVO:{}},
						{table:'23',list:[],orderVO:{}},
						{table:'24',list:[],orderVO:{}},
						{table:'25',list:[],orderVO:{}},
						{table:'26',list:[],orderVO:{}},
					]
			}
		},
		methods:{
			orderHander(table){
				// console.log("桌号："+table)
				if(this.rows[table-1].orderVO==null || this.rows[table-1].orderVO.id ==null || this.rows[table-1].orderVO==0){
					this.orderVO = orderVO()
					this.orderVO.tableId = table
				}else{
					this.orderVO = this.rows[table-1].orderVO
				}
				this.$router.push({name:'OrderTable',params:{orderVO:this.orderVO}})
			},
			getAllOrderMain(){
				//查询未结账的数据
				listOrderMain({status:1}).then(r=>{
					// console.log(JSON.stringify(r))
					if(r.data.code == 201){
						this.$router.push('/');
					}
					if(r.data && r.data.length !=0){
						for(var i =0;i<r.data.length; i++){
							this.getOrderSkuByOrderId(r.data[i])
						}
					}
				})
			},
			getType(item){
				console.log(item.status)
				if(item.status ==1 || item.status==2){
					return 'danger'
				}else{
					return 'info'
				}
			},
			getOrderSkuByOrderId(orderMain){
				listOrderSkuByOrderId({orderId:orderMain.id}).then(r=>{
					this.rows[orderMain.tableId-1].list = r.data.list
					this.rows[orderMain.tableId-1].orderVO = r.data
				})
			}
		},
		created:function(){
			console.log("created")
			this.getAllOrderMain()
		},
		beforeCreate() {
			console.log("beforecreate")
		}
	}
</script>

<style style scss scoped>
	.order{
	}
	.el-row {
		margin-top: 20px;
	    margin-bottom: 20px;
	        &:last-child {
	          margin-bottom: 0;
	        }
	  }
	  .el-col {
	    border-radius: 4px;
	  }
	  .bg-purple-dark {
	    background: #99a9bf;
	  }
	  .bg-purple {
	    background: #d3dce6;
	  }
	  .redCalss{
		  background:red;
	  }
	  .bg-purple-light {
	    background: #e5e9f2;
	  }
	  .grid-content {
		margin-top: 20px;  
	    border-radius: 4px;
	    min-height: 120px;
	  }
	  .row-bg {
	    padding: 10px 0;
	    background-color: #f9fafc;
	  }
	  .red{
		  background: red;
	  }
	  .medium{
		color: #475669;
		font-size: 14px;
		opacity: 0.75;
		line-height: 100px;
		margin: 0;
	  }
	
</style>
